.advantages {
  background-color: #f9fbfd;
  background-image: url("../img/background/middle_back_desktop.png");
  background-repeat: no-repeat;
  background-size: 100%;
  padding-top: 109px;
  padding-bottom: 120px;
}

@media (min-resolution: $retina) {
  .advantages {
    background-color: #f9fbfd;
    background-image: url("../img/background/middle_back_desktopx2.png");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.advantages__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0 3px;

  h2 {
    width: 100%;
    text-align: center;
    margin: 0;
    margin-bottom: 50px;
    font-weight: 600;
    font-size: 28px;
    line-height: 150%;
    color: #011c40;
  }
}

.advantages__block {
  box-sizing: border-box;
  width: 31.1%;
  background-color: #f9fbfd;
  min-height: 241px;
  padding: 30px;
  padding-right: 30px;
  padding-bottom: 25px;

  h3 {
    margin-top: 15px;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: #011c40;
  }

  p,
  div {
    margin: 0;
    font-weight: normal;
    font-size: 14px;
    line-height: 145%;
    color: #011c40;
  }
}

.advantages__block--ship {
  padding-right: 15px;
}

.advantages__block--arrow-yes {
  padding-top: 34px;
  padding-right: 50px;

  h3 {
    margin-top: 21px;
    margin-bottom: 8px;
  }
}

.advantages__block--zodiac > p {
  padding-right: 50px;
}

.advantages__block--zodiac {
  padding-top: 37px;

  h3 {
    margin-top: 24px;
  }

  div {
    padding-right: 60px;
  }
}

@media (max-width: $max-size) {
  .advantages {
    background-size: 1366px;
    padding: 40px 0;
  }
}

@media (max-width: $mid-size) {
  .advantages {
    background-color: #f9fbfd;
    background-image: url("../img/background/middle_back_tablet.png");
    background-repeat: no-repeat;
    background-size: 100%;
    padding-top: 90px;
    padding-bottom: 100px;

    h2 {
      font-size: 26px;
    }
  }

  @media (min-resolution: $retina) {
    .advantages {
      background: url("../img/background/middle_back_tabletx2.png") no-repeat;
      background-size: 100%;
    }
  }

  .advantages__wrapper {
    padding: 0;
  }

  .advantages__block {
    padding: 20px;
    padding-top: 25px;
    width: 30.5%;

    h3 {
      margin-bottom: 7px;
    }

    svg {
      transform: scale(0.9);
    }
  }

  .advantages__block--ship {
    padding-right: 15px;
    padding-top: 20px;

    svg {
      transform: scale(0.8);
    }

    h3 {
      margin-top: 10px;
    }
  }

  .advantages__block--arrow-yes h3 {
    margin-top: 18px;
  }

  .advantages__block--zodiac {
    svg {
      margin-top: 5px;
    }

    h3 {
      margin-top: 18px;
    }

    div,
    p {
      padding-right: 0;
      display: inline;
    }
  }

  .advantages__block--zodiac > p {
    padding-right: 0;
  }
}

@media (max-width: $min-size) {
  .advantages {
    background-color: #f9fbfd;
    background-image: url("../img/background/middle_back_mobile.png");
    background-repeat: no-repeat;
    background-size: 100%;
    flex-wrap: wrap;
    padding-top: 58px;
    padding-bottom: 35px;

    h2 {
      margin-bottom: 30px;
      font-size: 24px;
    }
  }

  @media (min-resolution: $retina) {
    .advantages {
      background: url("../img/background/middle_back_mobilex2.png") no-repeat;
      background-size: 100%;
    }
  }

  .advantages__block {
    width: 100%;
    margin-bottom: 30px;

    h3 {
      margin-top: 12px;
      width: 175px;
    }
  }

  .advantages__block--arrow-yes h3 {
    margin-top: 18px;
  }

  .advantages__block--zodiac {
    h3 {
      margin-top: 20px;
    }

    div {
      padding-right: 25px;
      display: block;
    }
  }

  .advantages__block--zodiac > p {
    padding-right: 0;
  }
}
